import { CButton, CCard, CCardBody, CCardGroup, CCol, CForm, CModalBody, CRow, CSpinner } from '@coreui/react';
import { Formik } from 'formik';
import React, { useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import 'react-tagsinput/react-tagsinput.css';
import * as Yup from 'yup';
import config from '../../../config';
import { SUCCESS } from '../../../store/callApi/actions';
import { CModal, CModalHeader } from '../../../components';
import { useApi } from 'store/callApi';
import { RenderInputPassword } from '../containers/extensions';

interface ModalChangePasswordProps {
  show: boolean;
  handleClose?: () => void;
  onSuccess?: () => void;
  setShowAttention: (show: any) => void;
}

const ChangePasswordSchema = Yup.object().shape({
  password: Yup.string()
    ?.required('Your password must be at least 8 characters')
    .matches(
      config.validate.validatePassword(),
      'Your password must be at least 8 characters long with one letter, and a number',
    ),
  newPassword: Yup.string()
    ?.required('Please Enter new password')
    .matches(
      config.validate.validatePassword(),
      'Your password must be at least 8 characters long with one letter, and a number',
    ),
  confirmNewPassword: Yup.string()
    ?.required('Please Enter confirm password')
    .oneOf([Yup.ref('newPassword'), null], 'Confirm password does not match with New Password')
    .matches(
      config.validate.validatePassword(),
      'Your password must be at least 8 characters long with one letter, and a number',
    ),
});

export const ModalChangePassword: React.FC<ModalChangePasswordProps> = (props) => {
  const { callApi } = useApi();
  const [loading, setLoading] = useState(false);
  const access_token = useSelector((state: any) => state.auth.access_token);
  const { handleClose, setShowAttention } = props;

  /**
   * When submit, call api to change password
   * @param values all values in form: old and new password
   */
  const onSubmit = (values: { password: string; newPassword: string; confirmNewPassword: string }) => {
    const bodyData: any = {
      old_password: values?.password,
      new_password: values?.newPassword,
    };

    setLoading(true);
    callApi(
      {
        method: 'put',
        api: config.rest.changeAdminPassword(),
        body: bodyData,
        token: access_token,
      },
      (response: any) => {
        setLoading(false);
        const { status } = response;
        if (status === SUCCESS) {
          handleClose?.();
          setShowAttention(true);
        }
      },
    );
  };

  const [showPassword, setShowPassword] = useState<{
    password: boolean;
    newPassword: boolean;
    confirmNewPassword: boolean;
  }>({
    password: false,
    newPassword: false,
    confirmNewPassword: false,
  });

  const ref = useRef<any>(null);

  const onCloseModal = () => {
    props?.handleClose?.();
    ref?.current?.resetForm?.();
  };

  return (
    <div className="flex-row align-items-center">
      <CModal
        size="lg"
        centered
        show={props.show}
        onClose={onCloseModal}
        onClosed={onCloseModal}
        closeOnBackdrop={false}
      >
        <CModalHeader closeButton>Change password</CModalHeader>
        <CModalBody>
          <CRow className="justify-content-center">
            <CCol md="12">
              <CCardGroup>
                <CCard>
                  <CCardBody>
                    <Formik
                      innerRef={ref}
                      enableReinitialize={true}
                      initialValues={{
                        password: '',
                        newPassword: '',
                        confirmNewPassword: '',
                      }}
                      onSubmit={onSubmit}
                      validationSchema={ChangePasswordSchema}
                    >
                      {({ values, errors, touched, dirty, handleChange, handleBlur, handleSubmit, isValid }) => {
                        const inputParams = {
                          values,
                          errors,
                          touched,
                          handleChange,
                          handleBlur,
                        };

                        return (
                          <CForm onSubmit={handleSubmit}>
                            <RenderInputPassword
                              name="password"
                              label="Old Password"
                              {...inputParams}
                              show={showPassword.password}
                              setShow={() => setShowPassword({ ...showPassword, password: !showPassword.password })}
                            />
                            <RenderInputPassword
                              name="newPassword"
                              label="New Password"
                              {...inputParams}
                              show={showPassword.newPassword}
                              setShow={() =>
                                setShowPassword({ ...showPassword, newPassword: !showPassword.newPassword })
                              }
                            />
                            <RenderInputPassword
                              name="confirmNewPassword"
                              label="Confirm Password"
                              {...inputParams}
                              show={showPassword.confirmNewPassword}
                              setShow={() =>
                                setShowPassword({
                                  ...showPassword,
                                  confirmNewPassword: !showPassword.confirmNewPassword,
                                })
                              }
                            />
                            <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
                              <CButton
                                disabled={!(isValid && dirty && !loading)}
                                color="primary"
                                className="px-4"
                                style={{ width: 100 }}
                                type="submit"
                              >
                                {loading ? <CSpinner size="sm" color="light" /> : 'Update'}
                              </CButton>
                            </div>
                          </CForm>
                        );
                      }}
                    </Formik>
                  </CCardBody>
                </CCard>
              </CCardGroup>
            </CCol>
          </CRow>
        </CModalBody>
      </CModal>
    </div>
  );
};
